* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/* BlogPosts.css */

.blog-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.blog-card {
  background-color: hsl(183, 100%, 15%);
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 10px;
  color: #9fe8df;
  width: calc(
    33.33% - 20px
  ); /* Adjust as needed for the desired number of cards in a row */
  box-sizing: border-box;
}

.blog-card h2 {
  margin-bottom: 10px;
  font-family: monospace;
  color: #7f9d9f;
  background-color: #26c2ae;
  border-radius: 10px;
  padding-left: 25px;
  /* white-space: nowrap; */
  font-size: 30px;
}

.header {
  font-size: 20px;
  margin-bottom: 5px;
  color: white;
  font-family: cursive;
  padding: 10px 20px;
  margin-right: 20px;
  margin-left: 20px;
  margin-top: 10px;
  text-align: center;
}

.blog-card p {
  margin: 5px 0;
  font-size: 15px;
  font-family: cursive;
}

.blog-card h5 {
  font-size: 20px;
  color: #ccc;
}

.blog-card .metadata {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.blog-card .metadata span {
  color: white;
}
