* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* NewBlogPost.css */
.new-blog-post-container {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  width: 300px;
}

.new-blog-post-container input,
.new-blog-post-container textarea {
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.new-blog-post-container button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  display: block;
}

.new-blog-post-container button:hover {
  background-color: #0056b3;
}
